.repertory {
  width: 98%;
  height: 98%;
  margin-left: 10px;
  margin-top: 10px;
  display: flex;
  overflow: scroll;

  &-header {
    width: 100%;
    height: 80px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &Left {
      display: flex;
      align-items: center;
      height: 100%;
      font-size: 14px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: rgba(34, 34, 34, 0.4);

      p {
        margin: 0;
        color: #222222;
      }
    }

    &Form {
      display: flex;
      height: 100%;
      align-items: center;

      &Item {
        margin-left: 15px;
        font-size: 16px;
        font-family: SourceHanSansSC-Normal, SourceHanSansSC;
        font-weight: 400;
        color: #222222;
        display: flex;
        align-items: center;
      }

      &Controls {
        width: 130px;
      }
    }

    &Right {
      display: flex;
      height: 100%;
      align-items: center;
      justify-content: flex-end;
    }
  }

  &-left {
    width: 270px;
    height: 100%;
    background: white;

    &Search {
      width: 170px;
    }
  }

  &-right {
    flex: 1;
    margin-left: 15px;
    background: white;
    overflow-y: scroll;
  }

  .ant-tree .ant-tree-node-content-wrapper.ant-tree-node-selected {
    background-color: #bae7ff;
  }
}
@primary-color: #1890ff;