.ellipsis {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 150px;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.ellipsis-box {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: keep-all;
}
@primary-color: #1890ff;