.repertory-table {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  &-clear {
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 14px;
    cursor: pointer;
  }

  &-advancedSearch {
    margin-left: 15px;
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 14px;
    cursor: pointer;
  }

  &-table {
    margin-top: 20px;
    width: 100%;
    min-height: 200px;
  }

  &-detail {
    font-size: 14px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 20px;
    text-shadow: -11px 2px 22px rgba(0, 0, 0, 0.16);
    cursor: pointer;
  }

  .ant-form-item {
    margin-bottom: 0px;
  }

  .ant-form-item-label>label {
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #222222;
    line-height: 14px;
  }

  .ant-form-item-label>label::after {
    content: "";
  }


}
@primary-color: #1890ff;